import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
const backgroundColors = ['rgb(63 63 63)', '#efefef'];
const iconColors = ['white', 'black'];

const Sidebar = () => {
    const dispatch = useDispatch();
    const { menuOpen, index,animating } = useSelector(state => state.dataReducer);
    useEffect(() => {
        if (menuOpen)
            document.getElementsByClassName('awssld--active')[0].style['transform'] = 'translate3d(-100px, 0px, -600px) rotateY(20deg)';
        if (!menuOpen)
            document.getElementsByClassName('awssld--active')[0].style['transform'] = 'translate3d(0,0,0)';
    }, [menuOpen])
    const setIndex = (ind) => {
        dispatch(
            {
                type: 'updateState',
                payload: {
                    index: ind,
                    animating:false
                }
            }
        )
    }

    const setAnimation = () => {
        dispatch(
            {
                type: 'updateState',
                payload: {
                    animating:true
                }
            }
        )
    }

    const hamburger = {
        position: 'fixed',
        top: '20px',
        right: '50px',
        cursor: 'pointer',
        zIndex: 20
    }

    const line = {
        width: '28px',
        height: '3px',
        background: iconColors[index],
        marginTop: '5px',
        transition: 'all 0.2s'
    }
    const menu = {
        position: 'fixed',
        width: '350px',
        bottom: 0,
        top: 0,
        background: backgroundColors[index],
        zIndex: 18,
        right: 0,
        color: iconColors[index]
    }

    return (
        <div>
            <div className='hamburger' style={!animating?{...hamburger}:{...hamburger,pointerEvents:"none"}} onClick={() => {

                dispatch({ type: 'updateState', payload: { menuOpen: !menuOpen } });

            }} >
                <div style={!menuOpen ? { ...line } : { ...line, opacity: 0 }} />
                <div style={!menuOpen ? { ...line } : { ...line, transform: ' rotate(45deg)' }} />
                <div style={!menuOpen ? { ...line } : { ...line, transform: 'translateY(-8px) rotate(-45deg)' }} />
            </div>
            <div className='menu' style={menuOpen ? menu : {...menu, width: '0',overflow:'hidden' }} >
                <div className='menu-inner' style={{ height: '100%', width: '100%', backdropFilter: 'blur(8px) brightness(0.8)', display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                    <div style={{ height: '45vh', display: 'flex', flexDirection: 'column', justifyContent: 'center', textAlign: 'center' }}>
                        <div className={index === 0 ? 'menu-items active' : 'menu-items'} onClick={(e) => {
                            let time = 0;
                            if (index != 0) {
                                let par = e.target.parentElement;
                                console.log(par.parentElement);
                                par.children[index].classList.toggle('active');
                                par.children[0].classList.toggle('active');
                                time = 400;
                                setAnimation();
                            }
                            setTimeout(() => setIndex(0), time);
                            // setTimeout(() => setShowMenu(), 2*time);
                        }}>
                            Home
                        </div>
                        <div className={index === 1 ? 'menu-items active' : 'menu-items'} onClick={(e) => {
                            let time = 0;
                            if (index != 1) {
                                let par = e.target.parentElement;
                                par.children[index].classList.toggle('active');
                                par.children[1].classList.toggle('active');
                                time = 500;
                                setAnimation();
                            }
                            setTimeout(() => setIndex(1), time);
                        }}>
                            Projects
                        </div>
                        <div className={index === 2 ? 'menu-items active' : 'menu-items'} onClick={(e) => {
                            let time = 0;
                            if (index != 2) {
                                let par = e.target.parentElement;
                                par.children[index].classList.toggle('active');
                                par.children[2].classList.toggle('active');
                                time = 500;
                                setAnimation();
                            }
                            setTimeout(() => setIndex(2), time);
                        }}>
                            About
                        </div>
                        <div className={index === 3 ? 'menu-items active' : 'menu-items'} onClick={(e) => {
                            let time = 0;
                            if (index != 3) {
                                let par = e.target.parentElement;
                                par.children[index].classList.toggle('active');
                                par.children[3].classList.toggle('active');
                                time = 500;
                                setAnimation();
                            }
                            setTimeout(() => setIndex(0), time);
                        }}>
                            Contact
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Sidebar;