import React from 'react';
import {ReactComponent as Github} from '../assets/icons/Github.svg';
import {ReactComponent as Gmail} from '../assets/icons/Gmail.svg';
import {ReactComponent as Linkedin} from '../assets/icons/Linkedin.svg';
import { useSelector } from 'react-redux';

const iconColors = ['white', 'black'];
const GmailIconGroup = () => {
    const {  index } = useSelector(state => state.dataReducer);

    const content = {
        display:'block',
        flexDirection:'column',
        position:'absolute',
        top:'20vh',
        left:'2em',
        height:'30vh',
    }

    const icon = {
        display:'flex',
        flex:1,
        cursor:'pointer',
        marginTop:'10vh',
        marginLeft:'10px',
        padding:'10px',
        width:'2em',
        height:'2em',
        fill:iconColors[index],
        stroke:iconColors[index]
        
    }
    return (
        <div style={content}>
            <Github style={icon} id='iconGit'/>
            <Gmail style={icon}  id='iconGit' />
            <Linkedin style={icon}  id='iconGit'/>
        </div>
    )
}

export {GmailIconGroup};