import React, { useEffect, useState } from 'react';
import SearchBar from "material-ui-search-bar";
import { useSelector, useDispatch } from 'react-redux';
import MenuItem from '@material-ui/core/MenuItem';
import Input from '@material-ui/core/Input';
import Select from '@material-ui/core/Select';

import './SearchFilter.css';
const styles = {
    search: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center'
    },
    filter: {
        marginTop: '2em',
        marginLeft: '2%',
        width: '200px',
        display: 'flex',
        justifyContent: 'space-between',
        backgroundColor: '#efefef80',
        height: '48px',
        position: 'relative'

    },
    chip: {
        margin: 2,
    },
    chips: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    icon: {
        paddingBottom: '10px',
        paddingLeft: '10px'
    },
    dark: {
        backgroundColor: 'green'
    }
}

const Filter = () => {

    const { tagList, tagSelected } = useSelector(state => state.projectReducer);
    const dispatch = useDispatch();
    const tagSelectedSet = new Set(tagSelected);
    return (
        <div className='filter' style={styles.filter}>
            <div className={'label'} id='label'>
                Filter
            </div>
            {/* {showTagList && <div className='tagList' >
                {tagList.map((val, id) => (
                    <MenuItem
                        classes={tagSelectedSet.has(val) ? ['dark'] : []}
                        onClick={(e) => {
                            console.log(e.target['textContent']);
                            dispatch({ type: 'setTagSelected', payload: e.target.textContent })
                        }}
                        className='tagItems' key={id} value={val}> {val}
                    </MenuItem>))}
            </div>}
            <input onChange={(e) => {
                setTags(e.target.value);
            }}
                onClick={(e) => { e.target.parentElement.children[0].classList.add('active'); setShowTagList(true); }}
                onBlur={(e) => { if (tags.length === 0) e.target.parentElement.children[0].classList.remove('active'); }}
                value={tags}></input> */}
            <Select
                className='select'
                multiple
                value={[]}
                onOpen={(e) => {
                    e.target.parentElement.parentElement.children[0].classList.add('active');
                }}
                onClose={(e) => {
                    console.log(document.getElementById('label'))
                    document.getElementById('label').classList.remove('active');
                }}
                onChange={(e) => dispatch({ type: 'setTagSelected', payload: e.target.value[0] })}
                input={<Input id="select-multiple-chip" />}
            >
                {tagList.map((tag) => (
                    <MenuItem key={tag} value={tag} style={tagSelectedSet.has(tag) ? { backgroundColor: 'green' } : {}}>
                        {tag}
                    </MenuItem>
                ))}

            </Select>
        </div >

    )
}


const SearchFilter = () => {

    const dispatch = useDispatch();


    const { filter, search } = useSelector(state => state.projectReducer);

    return (
        <div className='search' style={styles.search}>
            <SearchBar
                onChange={(value) => dispatch({ type: 'setSearch', payload: value })}
                value={search}
                style={{ width: '55%', minWidth: '200px', marginLeft: '2%', marginTop: '2em' }}
                placeholder={'technology/project/etc...'}
            />
            <Filter />

        </div>


    );
}

export default SearchFilter;