// will have all methods related to navbar

const projects ={
    'Chinese Character':['js','react','redux','HTML','CSS','SpringBoot','MongoDB','Selenium','Adobe XD'],
    'Chinese Character Landing Page' : ['js','react','HTML','CSS','Adobe XD'],
    'Todo List':['js','HTML','CSS'],
    'Css Demos': ['js','HTML','CSS'],
    'Shattering Text':['HTML','CSS','Figma','Algorithms'],
    'Sorting Visualizer' :['HTML','CSS','Algorithms'],
    'Maze Solver And Generator':['HTML','CSS','Algorithms']
};
const tagList=[...new Set(Object.values(projects).reduce((a,b)=>[...a,...b]))];
const defaultState = {
    projects:projects,
    tagList:[...tagList],
    tagSelected:tagList,
    filter:[],
    search:'',
}
const projectReducer = (state=defaultState,action) => {
    switch(action.type){
        case 'setFilter':
            return {...state,filter:action.payload};
        case 'setSearch':
            return {...state,search:action.payload};
        case 'setTagSelected':
            return {...state,
                tagSelected:
                    (new Set(state.tagSelected)).has(action.payload)?
                    state.tagSelected.filter((val)=>val!==action.payload):
                    [...state.tagSelected,action.payload]
                };
        default:
            return state;
    }
}

export default projectReducer;